import {Navbar, MobileNavMenu} from '../../Atoms';
import './header.css';
import {ReactComponent as Logo} from './unshutLogo.svg'
import {ReactComponent as MobileNav} from './mobileNav.svg'
import {ReactComponent as CloseButton} from './closeButton.svg'
import { useState } from 'react';


export function Header(){
    const [mobileNavActive, setIsMobileNavActive] = useState(false);

    const handleMobileClick = async() => {
     setIsMobileNavActive(!mobileNavActive);
    }
    return(
        <div className = 'header'>
            {/* <div id = 'headerTextDiv'>*/}
                <div className='logoArea'>
                    <div className='logo'> <Logo></Logo></div>
                    <div className= 'headerText'>THE BLACK BOX</div>
                </div>
                <div className='mobileNav'>
                    {/* svg file */}
                    <div className='mobileNavHolder' onClick={handleMobileClick}>{mobileNavActive ? <CloseButton/> : <MobileNav/>}</div>
                </div>
                <div className='navBar'>
                    <Navbar/>
                </div>
                
                <div className ='navSeparator'/>
               
                    <MobileNavMenu setIsMobileNavActive={setIsMobileNavActive} classToUse={mobileNavActive ?  "navArea.menu-active":"navArea" }/>
             
        </div>
            
       
    )
}